*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    background-color: rgb(17, 17, 17);
}

.navbar{
    position: fixed;
    width: 100%;
    height: 60px;
    top: 0;
    background-color: #111;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo{
    position: fixed;
    left: 50px;
    width: 107px;
}

.avatar{
    position: fixed;
    right: 50px;
    width: 30px;
}
.banner{
    width: 100%;
    height: 600px;
    margin-top: 60px;
    color: white;
    background-size: cover;
    object-fit: cover;
}

.banner .content{
    padding-top: 253px;
    height: 190px;
    margin-left: 90px;
}

.banner .content .title .title-head{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 1rem;
}

.banner .content .banner-buttons .button{
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding: 0.5rem 2rem 0.5rem;
    background-color: rgba(51, 51, 51, 0.5);
    cursor: pointer;
    margin-right: 1rem;
}

.banner .content .banner-buttons .button:hover{
    color: black;
    background-color: white;
}

.banner .content .description .description-head{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1.4rem;
    height: 80px;
    max-width: 560px;
    font-weight: 200;
    margin-top: 10px;
}

.banner .background-shade{
    height: 347px;
    background-image: linear-gradient(180deg, transparent, rgba(37, 37, 37, 0.61), #111);
}
.row{
    margin-left: 20px;
    color: white;
    margin-left: 50px;
    margin-top: 20px;
}

.row .posters{
    display: flex;
    padding: 20px;
    overflow-x: scroll;
    overflow-y: none;
}

.row .posters::-webkit-scrollbar{
    display: none;
}

.row .posters .posterImg{
    max-height: 250px;
    margin-right: 30px;
}

.row .posters .smallPoster{
    max-height: 164px;
    margin-right: 30px;

}

.row .posters{
    cursor: pointer;
}

.row .posters .posterImg:hover{
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
}

.row .posters .smallPoster:hover{
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
}
